import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { getSelisihTimbangan } from "../../actions/TotalanTimbanganAction";
import { renderFieldSelect } from "../../renderField";
import { getLokasi } from "../../actions/lokasiAction";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormSelisihTimbangStock extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getSelisihTimbangan(localStorage.getItem("noFaktur")));
    this.props.dispatch(getLokasi());
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  setSelisih() {
    this.props.change("total_netto_selisih", this.props.total_netto_selisih);
    this.props.change("total_bruto_selisih", this.props.total_bruto_selisih);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="kode_kelompok"
              component={renderFieldSelect}
              options={[
                {
                  value: "PERHIASAN",
                  name: "PERHIASAN",
                },
                {
                  value: "CIOK",
                  name: "CIOK",
                },
                {
                  value: "RONGSOK",
                  name: "RONGSOK",
                },
              ]}
              type="text"
              label="Kode Kelompok"
              placeholder="Masukan "
              disabled
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="tanggal"
              component={renderField}
              type="date"
              label="Tanggal"
              placeholder="Masukan Tanggal"
              readOnly
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="lokasi"
              component={renderFieldSelect}
              options={this.props.lokasi.map((list) => {
                let data = {
                  value: list.kode_lokasi,
                  name: list.nama_lokasi,
                };
                return data;
              })}
              type="text"
              label="Lokasi"
              placeholder="Masukan Lokasi"
              disabled
            />
          </div>
          <div className="col-lg-12">
            <hr />
          </div>
          <div className="col-lg-3">
            <Field
              name="total_gross"
              component={renderField}
              type="text"
              label="Total Gross"
              placeholder="Masukan Total Gross"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="total_pkg"
              component={renderField}
              type="text"
              label="Total Pkg"
              placeholder="Masukan Total Pkg"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="total_bruto"
              component={renderField}
              type="text"
              label="Total Bruto"
              placeholder="Masukan Total Bruto"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="total_netto"
              component={renderField}
              type="text"
              label="Total Netto"
              placeholder="Masukan Total Netto"
              readOnly
            />
          </div>
          <div className="col-lg-12">
            <hr />
          </div>
          {/* <div className="col-lg-3">
            <Field
              name="total_netto_fisik"
              component={renderField}
              type="text"
              label="Total Netto Fisik"
              placeholder="Masukan Total Netto Fisik"
              onChange={this.setSelisih()}
            />
          </div> */}
          {/* <div className="col-lg-3">
            <Field
              name="total_bruto_fisik"
              component={renderField}
              type="text"
              label="Total Bruto Fisik"
              placeholder="Masukan Total Bruto Fisik"
              onChange={this.setSelisih()}
            />
          </div> */}
          {/* <div className="col-lg-3">
            <Field
              name="total_bruto_selisih"
              component={renderField}
              type="text"
              label="Total Bruto Selisih"
              placeholder="Masukan Total Bruto Selisih"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="total_netto_selisih"
              component={renderField}
              type="text"
              label="Total Netto Selisih"
              placeholder="Masukan Total Netto Selisih"
              readOnly
            />
          </div> */}
        </div>
        <div className="col-lg-12 mt-2">
          <div className="text-right">
            <button
              className="btn btn-primary"
              type="submit"
              disabled={this.props.total_netto === undefined ? true : this.props.onSend}
            >
              {this.props.onSend ? (
                <span
                  className="spinner-border spinner-border-sm mr-3"
                  aria-hidden="true"
                ></span>
              ) : null}
             Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormSelisihTimbangStock = reduxForm({
  form: "FormSelisihTimbangStock",
  enableReinitialize: true,
})(FormSelisihTimbangStock);
const selector = formValueSelector("FormSelisihTimbangStock");
export default connect((state) => {
  const { total_netto, total_bruto } = selector(
    state,
    "total_netto",
    "total_bruto"
  );
  return {
    selisih_totalan: state.provinsi.selisih_totalan,
    lokasi: state.provinsi.lokasi,
    onSend: state.provinsi.onSend,
    initialValues: {
      kode_kelompok: state.provinsi.selisih_totalan.kode_kelompok,
      tanggal: state.provinsi.selisih_totalan.tanggal,
      lokasi: state.provinsi.selisih_totalan.kode_lokasi,
      total_gross: state.provinsi.selisih_totalan.total_gross,
      total_bruto: state.provinsi.selisih_totalan.total_bruto,
      total_pkg: state.provinsi.selisih_totalan.total_pkg,
      total_netto: state.provinsi.selisih_totalan.total_netto,
      total_netto_selisih:
        parseFloat(state.provinsi.selisih_totalan.total_netto || 0) -
        parseFloat(total_netto || 0),
      total_bruto_selisih:
        parseFloat(state.provinsi.selisih_totalan.total_bruto || 0) -
        parseFloat(total_bruto || 0),
    },
  };
})(FormSelisihTimbangStock);
