import React, { Component } from "react";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import HeaderCard from "../HeaderCard";
import { getSelisihTimbangan } from "../actions/TotalanTimbanganAction";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { AxiosMasterPost } from "../Axios";
import { berhasil, gagal } from "../Alert";
import { finishSend, progressSend } from "../actions/LogicAction";
import FormSelisihTimbangStock from "./Components/FormSelisihTimbangStock";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    selisih_totalan: state.provinsi.selisih_totalan.detail_barang,
  };
};
class SelisihTimbangan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kode_kategori",
          text: "Kode Jenis",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_modal",
          text: "Kode Awal",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "bruto",
          text: "Bruto",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "bruto_system",
          text: "Bruto System",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "bruto_selisih",
          text: "Bruto Selisih",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "netto",
          text: "Netto",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "netto_system",
          text: "Netto System",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },

        {
          dataField: "netto_selisih",
          text: "Netto Selisih",
          align: "right",
          headerAlign: "right",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
      ],
      data: [
        {
          noFaktur: "-",
          kodeKategori: "-",
          kadarCetak: 0,
          kadarTransaksi: 0,
          kodeJenis: "-",
          noUrut: "-",
          brutoSelisih: 0,
          nettoTimbang: 0,
          nettoData: 0,
          brutoTimbang: 0,
          nettoSelisih: 0,
          brutoData: 0,
        },
      ],
      listLokasi: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getSelisihTimbangan(localStorage.getItem("noFaktur")));
    this.props.dispatch(getUUID());
  }
  handleSubmit(data) {
    var simpan = {
      total_bruto: data.total_bruto,
      total_bruto_system: data.total_bruto,
      total_bruto_selisih: data.total_bruto_selisih,
      total_netto: data.total_netto,
      total_netto_system: data.total_netto,
      total_netto_selisih: data.total_netto_selisih,
      detail_barang: this.props.selisih_totalan,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost(
      `totalan-barang/selisih-totalan/end-totalan/${this.props.noFaktur}`,
      simpan
    )
      .then(() => this.props.dispatch(finishSend()))
      .then(() => berhasil("Selisih Berhasil Di Simpan"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => localStorage.removeItem("totalanTimbangan"))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => window.location.reload())
      .catch((err) =>
        gagal(`Gagal Simpan Selisih, Detail : ${err.response.data}`).then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Selisih Timbangan" />
          <div className="card-body">
            <div className="col-lg-12">
              <FormSelisihTimbangStock
                noFaktur={this.props.noFaktur}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </div>
            <br />
            <div className="col-lg-12">
              <GlobalTabel
                keyField="kodeKelompok"
                data={this.props.selisih_totalan}
                columns={this.state.columns}
                search
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(SelisihTimbangan);
